.App {
  text-align: center;
}

.imageFooter {
  width: 50px;
  height: 50px;
}

.imageMe {
  width: 100px;
  height: 100px;
  align-items: right;
  border-style: none;
}

.footer {
  position: -webkit-sticky;
  position: sticky;
  height: 100px;
  top: calc( 100vh - 100px );
  position: fixed; 
  text-align: center;
  display:"flex";
  justify-content: center;
  align-items: center;
  width: 80%;
  left:0;
  bottom:0;
  right:0;
}

.resume {
  padding: 10px;
  display: grid;
  grid-gap: 1rem;
}

.project {
    display: flex;
    align-items: center;
    flex-direction: column;
}
.crdProject {
    height: 500px;
    width: 500px;
    background-color: #e3f2fd;
    justify-items: center;
    margin: 10px;
}
.imgRMProject {
  height: 50%;
  width: auto;
}

